export default class Tooltip extends React.Component {
  static defaultProps = {
    text: 'placeholder',
  }

  render() {
    return (
        <div>
            <a data-tip={this.props.text} data-event='touchstart mouseenter' data-event-off="touchstart mouseout">
                {this.props.children}
            </a>
            <ReactTooltip place="bottom" type="info" effect="solid" globalEventOff='touchstart'/>
        </div>
    )
  }
}



